.Pets {
  margin: 0 auto;
  padding: 20px;
  display: flex;
  justify-content: center;
  padding-bottom: 124px;
}

.BirthdayContainer {
  display: flex;
  text-align: center;
  justify-content: center;
  background-color: #ff5500;
  color: white;
  flex-direction: column;
  margin: 0px;
}

.BirthdayContainer > a {
  text-decoration: none;
  color: white;
}

.BirthdayText {
  margin: 5px;
  text-decoration: none;
}

.selectContainer
  {
  font-family: "Dosis", sans-serif;
  background-color: rgb(232, 240, 254);
  width: 30vw;
  height: 60px;
  padding: 10px;
  margin-bottom: 10px;
  border: none;
  color: rgb(74, 73, 73);
  font-weight: bolder;
}

.button {
  display: flex;
  background-color: #ff5500;
  color: #fff;
  text-decoration: none;
  padding: 10px 30px;
  border-radius: 5px;
  font-weight: bold;
  font-family: "Dosis", sans-serif;
  border: none;
  font-size: inherit;
  letter-spacing: 2px;
  cursor: pointer;
}

.PetList {
  width: 35vw;
  font-size: large;
  display: flex;
  flex-direction: column;
  margin-right: 60px;
}

.InputContainer {
  width: 35vw;
}

.dataContainer {
  display: flex;
  flex-direction: column;
}

.delete {
  background-color: #333;
  color: #fff;
  text-decoration: none;
  padding: 10px 30px;
  border-radius: 5px;
  font-weight: bold;
  font-family: "Dosis", sans-serif;
  border: none;
  font-size: inherit;
  letter-spacing: 2px;
  cursor: pointer;
  margin-top: 5px;
}

.child {
  display: flex;
  flex-direction: row;
  justify-content: center;
  font-size: 25px;
  overflow-wrap:anywhere;
}

.profilePic {
  width: 150px;
  border-radius: 50%;
  margin: 5px;
}

.female {
  background-color: rgb(255, 110, 159, 0.5);
  border: 10px solid #333;
  padding: 10px;
  margin-bottom: 10px;
}

.male {
  background-color: rgba(122, 146, 254, 0.5);
  border: 10px solid #333;
  padding: 10px;
  margin-bottom: 10px;
}
